import Link from 'next/link'
import { MoreHorizontal, Pencil, Trash } from 'lucide-react'
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import { Button } from "@/components/ui/button"
import { FormatDate } from '@/components/FormateDate'


export function NoteCard(note: any) {
  note = note.note
  return (
    <Card className="w-full bg-gray-50 dark:bg-gray-600 border-0 shadow-sm">
      <CardHeader className="flex flex-row items-start justify-between space-y-0 pb-2">
        <Link href={`/notes-detail?id=${note.id}`} className="flex-1">
          <CardTitle className="text-xl font-extrabold line-clamp-1 hover:text-gray-600 dark:hover:text-gray-300 transition-colors">
            {note.title}
          </CardTitle>
        </Link>
        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <Button variant="ghost" className="h-8 w-8 p-0">
              <MoreHorizontal className="h-5 w-5" />
              <span className="sr-only">Open menu</span>
            </Button>
          </DropdownMenuTrigger>
          <DropdownMenuContent align="end" className="w-40">
            <DropdownMenuItem
              className="flex items-center gap-2 cursor-pointer"
              // onClick={onEdit}
            >
              <Pencil className="h-5 w-5" />
              <span>Edit</span>
            </DropdownMenuItem>
            <DropdownMenuItem
              className="flex items-center gap-2 cursor-pointer text-red-600 focus:text-red-600"
              // onClick={onDelete}
            >
              <Trash className="h-5 w-5" />
              <span>Delete</span>
            </DropdownMenuItem>
          </DropdownMenuContent>
        </DropdownMenu>
      </CardHeader>
      <CardContent>
        <p className="text-sm text-gray-600 dark:text-gray-200 line-clamp-3 mb-2">
          {note.content}
        </p>
        <p className="text-xs text-gray-400 dark:text-gray-400">
          {FormatDate(note.updatedAt)}
        </p>
      </CardContent>
    </Card>
  )
}
